<template>
  <div class="detail-wrap pt-3">
    <div class="big-mode d-sm-block d-none">
      <div class="d-flex mb-3">
        <section class="w-50 ">
          <img :src="productUrl" alt="" class="w-100">
        </section>
        <section class="w-50">
          <div class="d-flex border-bottom pb-3">
            <div>
              <h5 class="text-title">{{productFullTitle}}</h5>
              <h6>商品編號: {{productFullNum}}</h6>
            </div>
            <div class="ml-auto text-price d-flex mt-6">
              <h5>
                NT$
              </h5>
              <span class="big-price">
                {{allData.price}}
              </span>
            </div>
          </div>
          <div class="d-flex flex-column mt-3 border-bottom">
            <div class="d-flex mb-3">
              <a v-for="(item,index) in allData.colors" :key="index" class="pt-4 pl-4 mr-3 d-block border bordeer-white" :style="{backgroundColor:item.color}" @click="colorObjectSelect = item, sizeObjectSelect = item.sizes[0]">
              </a>
            </div>
            <div class="d-flex mb-3">
              <a v-for="(item,index) in colorObjectSelect.sizes" :key="index" class="mr-3 py-1 px-6 size-checkbox bg-sizebg" @click="sizeObjectSelect = item">
                {{item.name}}
              </a>
            </div>
            <div class="d-flex ml-auto h6">
              <a href="" class="mr-3">
                評價
              </a>
              <a href="" class="mr-3">
                產品說明和尺寸表
              </a>
              <a class="" href="">
                收藏商品
              </a>
            </div>
          </div>
          <div class=" my-3 d-flex align-items-center">
            <h6 class="text-primary mb-0 mr-1">數量</h6>
            <div class="d-flex">
              <a class="flex-center border px-1 bg-sizebg" @click="qtyChange(-1)">-</a>
              <input type="text" name="" id="" class="qty-input border text-center" v-model.number="qty">
              <a class="flex-center border px-1 bg-sizebg" @click="qtyChange(1)">+</a>
            </div>
            <h6 @click="addToCart" class="add-cart bg-primary text-white rounded px-3 py-2 mb-0 ml-5" href="">加入購物車</h6>
          </div>
          <h6 class="text-price mb-3">超商取貨付款．滿千免運</h6>
        </section>
      </div>
      <section class="text-center">
        <img class=" w-75 mb-3" v-for="n in allData.introductionImgNum" :key="n" :src="introductionUrl(n)" alt="">
      </section>
    </div>
  <div class="mobile-mode d-block d-sm-none position-relative">
    <div class="w-75 position-relative mx-auto">
      <span class="slick-next slick-arrow position-absolute">&#62;</span>
      <span class="slick-prev slick-arrow position-absolute"> &#60;</span>
      <div class="slick-group">
        <img v-for="ImgNum in allData.introductionImgNum" :key="ImgNum" :src="introductionUrl(ImgNum)" class="w-100">
      </div>  
    </div>
      <div class="d-flex p-3">
       <h5>{{allData.title}}</h5> 
       <span class="text-price ml-auto d-flex">
        <h5>$</h5>
        <h3>{{allData.price}}</h3>
      </span>
       
    </div>
    </div>  
  </div>
</template>
<script>
import $ from "jquery"
import slick from 'slick-carousel'
export default {
  name: 'detail',
  data() {
    return {
      allData: {
        productId: ''
      },
      colorObjectSelect: {},
      sizeObjectSelect: {},
      qty: 1,
    }
  },
  computed: {
    productUrl() {
      const url = require(`@/assets/productsData/${this.allData.productId}/${this.colorObjectSelect.num}.jpg`)
      return url
    },
    productFullNum() {
      return this.allData.productId + this.colorObjectSelect.num + this.sizeObjectSelect.num
    },
    productFullTitle() {
      return `${this.allData.title}-${this.allData.gender} (${this.colorObjectSelect.name}-${this.sizeObjectSelect.name})`
    }
  },
  methods: {
    addToCart() {
      let bill = {
        "qty": this.qty,
        "id": this.allData.productId,
        "colorObject": this.colorObjectSelect,
        "sizeObject": this.sizeObjectSelect,
        "title": this.allData.title,
        "imgUrl": this.productUrl,
        "gender": this.allData.gender,
        "totalPrice": this.qty * this.allData.price,
        "price": this.allData.price,
        "fullId" : this.allData.productId + this.colorObjectSelect.num +this.sizeObjectSelect.num
      }
      this.$store.commit('ADDTOCART', bill)
    },
    qtyChange(num) {
      if (num == -1 && this.qty == 1) {} else {
        this.qty += num
      }
    },
    introductionUrl(n) {
      const url = require(`@/assets/productsData/${this.allData.productId}/D${n}.jpg`)
      return url
    },
  },
  created() {
    this.allData = require(`@/assets/productsData/${this.$route.params.productId}/data.json`)
    this.colorObjectSelect = this.allData.colors[0]
    this.sizeObjectSelect = this.colorObjectSelect.sizes.find(function(item) {
      if (item.isAvailable = true) {
        return item
      }
    })
  },
  mounted() {
    $('.slick-group').slick(
      {
        "nextArrow": $('.slick-next'),
        "prevArrow": $('.slick-prev'),
      }
    );
  }
}

</script>
<style lang="scss">

.qty-input {
  width: 30px;
}

.big-price {
  font-size: 70px;
  line-height: 70px;
}
.detail-wrap{
  background-color: #f3f3f3;
  @media (min-width: 575.98px) {
    background-color: white;
  }
}
.add-cart:hover {
  cursor: pointer;
  background-color: brown;
}
.slick-arrow{
  position:absolute;
  font-size:40px;
  color: white;
  opacity: 0.5;
  top:50%;
  z-index: 2000;
  transform:translateY(-50%);
  &:hover{
    cursor:pointer;
    opacity:1;
  }
}
.slick-next{
  right:0;
}
</style>
